section {
    background-color: #111d5e;
    transition: background-color 0.5s ease-out;
}
.btn-primary {
    background-color:  #111d5e;
    border-color:  #111d5e;
}
#quote-box {
    background-color: white;
    max-width: 600px;
    margin: auto;
}

.quote-icon, #text {
    font-size: 1.8rem;
    color: #111d5e;
}
a:hover {
    cursor: pointer;

}
.content {
    opacity: 1;
    transition: opacity, .5s, fade-out;
}
.link {
    background-color: #111d5e;
    color: white;
    padding: 0.375rem 0.75rem;
    
}
.quote-animate {
    opacity: 0;
    transition: opacity, .5s, fade-in;
}